import React from 'react';
import perfectDay from '../assets/perfect-day.svg';
import Classes from './CityComponent.module.css';

const CityComponent = React.forwardRef((props,ref) => {

    return (
        <>
            <img className={Classes.weatherLogo} src={perfectDay} alt='Perfect-Day'/>
            <span className={Classes.chooseCityLabel}>Find Weather Of Your City</span>
            <form className={Classes.searchBox} onSubmit={props.onClickSearch}>
                <input type="text" placeholder="City" id="city" ref={ref}/>
                <button id='search_btn'>Search</button>
            </form>
        </>
    )
})


export default CityComponent;