import React, { useState, useEffect, useRef, useReducer, Suspense, useCallback } from "react";
import axios from "axios";
import styled, { keyframes } from "styled-components";
import CityComponent from "./modules/CityComponent";
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import {Animate} from './modules/Animate';

const WeatherComponent = React.lazy(()=>import('./modules/WeatherInfoComponent'));

const ContainerBody = styled.div`
  font-family: "Open Sans", sans-serif;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(#035397, #eee);
`;

const animateContainer = keyframes`
    0%{
        opacity: 0;
        transform: translateY(-100%);
      }

    80%{
      transform: translateY(10%);
    }

    100%{
        opacity: 1;
        transform: translateY(0);
      }
`;

const Container = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  box-shadow: 0 3px 6px 0 #555;
  background: white;
  width: 25%;
  padding: 20px 10px;
  border-radius: 4px;
  font-family: "Montserrat";
  animation: ${animateContainer} 500ms linear;
      @media(max-width: 500px){
        width: 80%;
      }

      @media(min-width:500px) and (max-width: 1000px){
        width: 50%;
      }
`;

const AppLabel = styled.span`
  color: black;
  font-size: 18px;
  font-weight: bold;
`;

const LeftArrow = styled.div`
  width: 20px;
  height: 20px;
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
`;

const APP_KEY = "2d81fef83b338fbb183f884a19152898";

const initialState = {
  searchWeatherState: true,
};

function reducer(state = initialState, options) {
  switch (options.type) {
    case "SEARCH_WEATHER_TRUE":
      return {
        searchWeatherState: true,
      };
    case "SEARCH_WEATHER_FALSE":
      return {
        searchWeatherState: false,
      };
    default:
      return state;
  }
}

function Weather() {
  const [{ searchWeatherState }, dispatch] = useReducer(reducer, initialState);

  //create a ref to get value of input tag from city component using forward ref
  const inputElementRef = useRef();

  //Redux
//   const {city, weather, error} = useSelector(state => state);
//   const dispatchAction = useDispatch();

const [city, setCity] = useState();
const [weather, setWeather] = useState();
const [error, setError] = useState(false);

  useEffect(() => {
    async function fetch() {
      if (city) {
        try{
          const response = await axios.get(
            `https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=${APP_KEY}`
          );
          //Update weather state when fetched from API
          // props.updateWeather(response.data);
        //   dispatchAction({type: 'CHANGE_WEATHER', weather: response.data});
            setWeather(response.data);
        }
        catch(err){
        //   dispatchAction({type: 'FETCH_FAILED'});
            setError(true);
        }
      }
    }
    fetch();
  }, [city]);

  const onSubmitHandler = async (event) => {
    event.preventDefault();
    let enteredCity = inputElementRef.current.value;
    //update back button state to False
    dispatch({ type: "SEARCH_WEATHER_FALSE" });
    //Update CITY state when click search button
    // props.updateCity(enteredCity);
    // dispatchAction({type: 'CHANGE_CITY', city: enteredCity});
        setCity(enteredCity);
  };

  //passing dependency array is of no use here, just passed to avoid warnings
  const MemoisedSubmitHandler = useCallback(onSubmitHandler,[]);

  const backTOSearchWeather = () => {
    //update back button state to TRUE
    dispatch({ type: "SEARCH_WEATHER_TRUE" });
    //reset weather details when user click back button
    // props.updateWeather();
    // dispatchAction({type: 'CHANGE_WEATHER'});
    setWeather(null);
    setError(false);
  };

  let Component = (<CityComponent
    ref={inputElementRef}
    // onClickSearch={onSubmitHandler}
    onClickSearch={MemoisedSubmitHandler}
  />);

  //Check if No Error Occured and Weather is fetached properly
  if(!error && weather && !searchWeatherState){
    Component = (
      <Suspense fallback={<div>Loading...!</div>}>
        <WeatherComponent weatherInfo={weather}/>
      </Suspense>
    )
  }
  else if(error){
    Component = (<div style={{color: '#FF3D68'}}>Please Enter Correct City Name</div>);
  }

  //render back arrow
  let leftArrow = null;

  if((weather && !searchWeatherState) || error){
    leftArrow = (
      <LeftArrow onClick={backTOSearchWeather}>
      <ArrowBackIosIcon />
    </LeftArrow>
    )
  }

  return (
    <ContainerBody>
      <Animate />
      <Container>
        <AppLabel>React Weather App</AppLabel>
        {leftArrow}
        {Component}
      </Container>
    </ContainerBody>
  );
}

export default Weather;
